import React, { useEffect, useState } from 'react';
import styled, { css } from 'styled-components';
import { colors } from '../../constants/colors';
import useWording from '../../utils/useWording';
import { respondFrom, respondTo } from '../../utils/responsive';
import Container from '../../components/Container';
import Text from '../../components/Text';

const PassAndFeastPage = () => {
  const wording = useWording('pass_and_feast');
  const [ isHighlight, setIsHighlight ] = useState(false);

  function handleClickNote() {
    setIsHighlight(true);
  }
  function handleClickCover() {
    setIsHighlight(false);
  }
  
  return (
    <Root>
      <Title>{ wording.title }</Title>
      <Description dangerouslySetInnerHTML={{ __html: wording.description }} />
      <Buttonbar>
        <button>{ wording.mint }</button>
        <div className="note" onClick={handleClickNote}>{ wording.note }</div>
      </Buttonbar>
      <Cover display={ isHighlight ? 'show' : 'hide'} onClick={handleClickCover} />
      <List>
        { wording.list.map((item, i) => 
          <li key={i}>
            <NFTItem display={ item.type === 'heading' ? 'active' : '' } data={item} />
          </li>
        ) }
      </List>
    </Root>
  )
}

const NFTItem = ({ data, ...props }) => {
  const { type, content, title, image } = data;
  const [ isActive, setIsActive ] = useState(false);

  function handleClickItem() {
    setIsActive(!isActive);
  }
  
  return (
    <NFTItemRoot
      type={type}
      focusDisplay={isActive ? 'active' : 'normal'}
      onClick={handleClickItem}
      {...props}>
      { type === 'icon' && 
        <div className="icon">
          <img src={image} />
          <Text as="p">{ title }</Text>
        </div>
      }
      { type === 'heading' && <Text className="text">{ content }</Text> }
    </NFTItemRoot>
  )
}

const Root = styled(Container)`
  padding-bottom: 200px;
  box-sizing: border-box;
  ${respondTo.md} {
    padding-top: 80px;
  }
`
const Title = styled(Text)`
  margin-bottom: 20px;
  color: ${colors.black};
  font-size: 36px;
  font-weight: 700;
  ${respondTo.md} {
    font-size: 22px;
    white-space: nowrap;
  }
`
const Description = styled(Text)`
  font-size: 18px;
  color: ${colors.block};
  white-space: break-spaces;
  ${respondTo.md} {
    font-size: 16px;
  }
  strong {
    font-size: 22px;
    font-weight: 700;
  }
`
const Buttonbar = styled.div`
  display: flex;
  align-items: center;
  margin-top: 20px;
  > button {
    border: 0;
    border-radius: 4px;
    padding: 8px 20px;
    height: 45px;
    font-size: 20px;
    font-weight: 500;
    color: ${colors.white};
    background-color: ${colors.orange};
    ${respondTo.md} {
      font-size: 14px;
    }
  }
  > .note {
    margin-left: 20px;
    font-size: 20px;
    color: ${colors.trueBlack};
    text-decoration: underline;
    cursor: pointer;
    ${respondTo.md} {
      font-size: 14px;
    }
  }
`

const Cover = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: ${colors.black};
  z-index: 2;
  transition: all .3s ease;
  ${({ display }) => display === 'show' && css`
    opacity: 0.5;
    visibility: visible;
  `}
  ${({ display }) => display === 'hide' && css`
    opacity: 0;
    visibility: hidden;
  `}
`

const List = styled.ul`
  display: flex;
  flex-wrap: wrap;
  margin: 0 -6px;
  margin-top: 40px;
  > li {
    padding: 6px;
    width: 25%;
    box-sizing: border-box;
    ${respondTo.lg} {
      width: 33.33%;
    }
    ${respondTo.md} {
      width: 50%;
    }
  }
`

const NFTItemRoot = styled.div`
  position: relative;
  height: 0;
  padding-bottom: 100%;
  ${({ type }) => type === 'heading' && css`
    background-color: ${colors.orange};
  `}
  ${({ type }) => type === 'icon' && css`
    &:before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      border: 1px solid ${colors.black};
      width: calc(100% - 2px);
      height: calc(100% - 2px);
      z-index: 1;
    }
  `}
  ${({ display }) => display === 'active' && css`
    z-index: 2;
  `}
  > .icon {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    p {
      position: absolute;
      left: 0;
      top: 0;
      padding: 10px;
      width: 100%;
      height: 100%;
      background: ${colors.black};
      color: ${colors.white};
      font-size: 52px;
      font-weight: 700;
      white-space: break-spaces;
      box-sizing: border-box;
      z-index: 1;
      transition: opacity .3s ease;
      opacity: 0;
      ${respondTo.md} {
        font-size: 7vw;
      }
    }
    ${respondFrom.md} {
      &:hover p {
        opacity: 1;
      }
    }
    img {
      display: block;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
  > .text {
    padding: 10px;
    font-size: 52px;
    font-weight: 700;
    white-space: break-spaces;
    ${respondTo.md} {
      font-size: 7vw;
    }
  }
  ${({ focusDisplay }) => focusDisplay === 'active' && css`
    ${respondTo.md} {
      > .icon > p { opacity: 1; }
    }
  `}
`

export default PassAndFeastPage;